@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
.goods{display:flex;flex-direction:column;border-radius:16px;padding:16px;background:linear-gradient(to right, #a1ffce, #faffd1)}.goods__list{list-style:none}.goods__list::after{display:inline-block;content:"";width:80%;height:4px;border-radius:4px;background-color:#f7b733}.goods__buttons{display:flex;flex-direction:column;align-items:center;margin-top:24px}.goods__select{border-radius:4px}.button{margin-top:8px;font-family:"Lato", sans-serif;width:120px;height:40px;font-size:20px;border-radius:12px;background:linear-gradient(to right, #fc4a1a, #f7b733);border:1px solid #f7b733}.button:hover{background:transparent;color:#fc4a1a}

*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Lato", sans-serif;font-size:20px;height:100vh;background:linear-gradient(to right, #4ac29a, #bdfff3)}.App{margin:0 auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center}.App__title{padding-top:24px}

